      </template>
      <!--  -->
      <template v-if="TableData.auditor_status == 1">
        <el-button type="primary" @click="audit('getauidtorPass')" v-if="authority.b_pass==1"></el-button>
        <el-button @click="audit('getauditorInvalid')" v-if="authority.b_invalid==1"></el-button>
      </template>
      <!--  -->
