<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportSalesoutstorageSee",
  data() {
    return {
      authority: {},
      TableData: {
        goods_info: [] //
      }
    };
  },
  computed: {
    ...mapGetters(["inventoryoutinTirebackstorageSeeGet"])
  },
  methods: {
    ...mapActions(["inventoryoutinIndexACT"]),
    goBack() {
      this.inventoryoutinIndexACT({
        moduleName: "Index"
      });
    },
    audit(api) {
      //
      this.$axMethod[api]({
        outstock_id: this.TableData.outstock_id
      }).then(({ data }) => {
        if (data.code == 200) {
          this.$message({
            message: data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    initData() {
      //
      let outstock_id = "";
      outstock_id = this.inventoryoutinTirebackstorageSeeGet.outstock_id;
      this.$axMethod
        .getcheckOutstock({
          outstock_id: outstock_id
        })
        .then(({ data }) => {
          if (data.code == "200") {
            this.$set(this, "TableData", data.data);
          }
        });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if ([5, 7].includes(index)) {
            sums[index] = parseFloat(sums[index]);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.TableData.outstock_id = this.inventoryoutinTirebackstorageSeeGet.outstock_id;
    if (this.inventoryoutinTirebackstorageSeeGet.outstock_id) this.initData();
  }
};
</script>

