<template>
  <div>
    <el-dialog :close-on-click-modal="false" title="" :visible="true" class="dialogForm" width="1300px" @close="closeDialog">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <!-- <el-form-item label="" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="" end-placeholder="" clearable>
            </el-date-picker>
          </el-form-item>
          <el-form-item label="" size="mini">
            <el-input v-model="params.factory_appraisal_sn" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList.options"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary"></el-button>
            <el-button plain @click="onReset" size="mini"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="450" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="factory_appraisal_sn" width="180" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="supplier_name" width="150" label="()">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="num" width="150" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="180" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="200" label="">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" width="150" label="">
          </el-table-column>
        </el-table>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog()"> </el-button>
        <el-button class="tx" type="primary" @click="dialogSubmitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {
  getData,
  getCompanyListCommon,
  getDepartmentListCommon
} from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      authority: {},
      radio: "",
      TableData: {
        tableData: []
      },
      departmentList: {
        options: []
      },
      companyList: [],

      params: {},
      per_page: [30, 50, 100, 500],
      loading: true,
      searchParams: {},
      goPage: 1, //
      pageSize: 30,
      timeValue: [],
      dialogmultipleSelection: [], //
      setDate: []
    };
  },
  props: ["message", "mesCompanyID"],
  methods: {
    handleSelectionChange(val) {
      this.dialogmultipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      this.params.supplier_id = this.message;
      this.$axios({
        method: "get",
        url: this.$api.getSupplierAppraisalListPublics,
        params: this.params
      }).then(res => {
        if (res.data.code == 200) {
          this.TableData.tableData = res.data.data;
        }
        // this.TableData.tableData = res.data.data;
      });
      // Object.assign(this.searchParams.params, this.params, {
      //   page: 1
      // });
      // getData(this.searchParams);
    },
    onReset() {
      /**   */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.params.supplier_id = this.message;
      this.$axios({
        method: "get",
        url: this.$api.getSupplierAppraisalListPublics,
        params: this.params
      }).then(res => {
        if (res.data.code == 200) {
          this.TableData.tableData = res.data.data;
        }
      });
      // this.$set(this, "searchParams", {
      //   params: { supplier_id: this.message },
      //   TableData: this.TableData, //
      //   API: "getSupplierAppraisalListPublics", //
      //   vm: this //this
      // });
      // getData(this.searchParams);
    },
    closeDialog() {
      // 
      this.$parent.salesDocumentsV = false;
    },
    companyChange(val) {
      getDepartmentListCommon(this, this.params);
      this.$set(this.params, "department_id", "");
    },
    dialogSubmitForm() {
      //
      let This = this;
      var result = this.dialogmultipleSelection.map(item => {
        return item.factory_appraisal_id;
      });
      this.$axMethod
        .getAppraisalList({
          factory_appraisal_id: result.toString()
        })
        .then(response => {
          if (response.data.code == 200) {
            this.$set(this.$parent, "TableData", response.data.data);
            for (let i = 0, len = response.data.data.length; i < len; i++) {
              This.$emit("getAlloca", i);
            }
            this.$parent.salesDocumentsV = false;
          }
        });
    }
  },
  mounted() {
    this.$axios({
      method: "get",
      url: this.$api.getSupplierAppraisalListPublics,
      params: {
        supplier_id: this.message,
        company_id: this.mesCompanyID
      }
    }).then(res => {
      if (res.data.code == 200) {
        this.TableData.tableData = res.data.data;
      }
    });
    // this.$set(this, "searchParams", {
    //   params: { supplier_id: this.message }, //1
    //   TableData: this.TableData, //
    //   API: "getSupplierAppraisalListPublics", //
    //   vm: this //this
    // });
    // getData(this.searchParams); //
    getCompanyListCommon(this); //
  }
};
</script>

