<script>
import { mapActions, mapGetters } from "vuex";
import lodopDelivery from "common/components/public/PublicPrint.vue"; //
export default {
  name: "reportSalesOutSee",
  data() {
    return {
      authority: {},
      sum: null,
      tableData: [],
      params: {},
      title: null
    };
  },
  computed: {
    ...mapGetters(["inventoryoutinSalesOutSeeGet", "printingStatusGet"])
  },
  methods: {
    ...mapActions(["inventoryoutinIndexACT", "printingStatus"]),
    goBack() {
      this.inventoryoutinIndexACT({
        moduleName: "Index"
      });
    },
    lodopDe(name) {
      this.printingStatus(true);
      this.$set(this, "title", name);
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf({
          outstock_id: this.params.outstock_id
        });
      });
    },
    auditOrUnaudit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          outstock_id: this.params.outstock_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    }
  },
  components: {
    lodopDelivery
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$axios({
      method: "get",
      url: this.$api.salesCheckOutstock,
      params: {
        outstock_id: this.inventoryoutinSalesOutSeeGet.outstock_id,
        outstock_sn: this.inventoryoutinSalesOutSeeGet.outstock_sn
      }
    }).then(res => {
      if (res.data.code == 200) {
        this.params = res.data.data;
        this.tableData = res.data.data.goods_info;
      }
    });
  }
};
</script>

