<script>
import {
  getData,
  getInventoryFinanceCompanyOpen,
  tableResize //
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportInventoryoutinIndex",
  data() {
    return {
      authority: {},
      typelist: [],
      params: {},
      searchParams: {},
      listShow: false,
      timeValue: [],
      exportType: null,
      warehouseList: {
        options: []
      }, //
      TableData: {
        allList: {},
        tableData: []
      },
      departmentList: {
        options: []
      },
      companyList: [],
      params: {
        company_id: Number(localStorage.company_id)
      },
      goPage: 1, //
      pageSize: 30,
      per_page: [30, 50, 100, 500],
      loading: false,
      options4: [],
      list: [],
      GoodsTire: {}, //
      dialogFormVisibleSee: false,
      tableHeight: 600,
      price: 0,
      flag: false
    };
  },
  computed: {
    ...mapGetters(["inventoryoutinIndexGet"])
  },
  methods: {
    ...mapActions([
      "inventoryoutinInstorageSeeACT",
      "inventoryoutinRefundSeeACT",
      "inventoryoutinThreeinstOrageSeeACT",
      "inventoryoutinItheninstOrageSeeACT",
      "inventoryoutinTransferwareSeeACT",
      "inventoryoutinAuditreeoutSeeACT",
      "inventoryoutinSalesalLocationSeeACT",
      "inventoryoutinSalesOutSeeACT",
      "inventoryoutinSalesReturnSeeACT",
      "inventoryoutinThreeoutstOrageSeeACT",
      "inventoryoutinSalesoutstorageSeeACT",
      "inventoryoutinThreeinChangeSeeACT",
      "inventoryoutinSalescommissionSeeACT",
      "inventoryoutinSalessettlementSeeACT",
      "inventoryoutinTirebackstorageSeeACT"
    ]),

    onReset() {
      this.timeValue = [];
      this.typelist = [];
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id)
      });
      this.listShow = false;
    },
    renderHeaderpro() {
      return (
        <el-tooltip class="item" content="Bottom center" effect="light">
          <p slot="content"></p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            
          </span>
        </el-tooltip>
      );
    },

    exportOrder(scheduleType) {
      // 
      if (this.listShow == false) {
        this.$message({
          message: "",
          type: "error"
        });
        return false;
      }
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportInventoryRunningUrl(this.params).then(response => {
        if (response.data.code == 200) {
          window.location.href = response.data.data.url;
        }
      });
    },
    companyChange() {
      this.$set(this.params, "warehouse_id", " ");
      this.$set(this.params, "goods_name", " ");
    },
    goSaleputSee(row) {
      this.$router.push({
        path: "/sales/salesout/salesamend",
        query: {
          id: row.outstock_id,
          printSstatus: row.after_auditor_print_status
        }
      });
    },
    seeF(scope, row) {
      let orderId = row.order_sn.slice(-6); // id
      let orderName = row.order_sn.slice(0, 4);
      let goods_id = row.goods_id;
      let order_sn = row.order_sn;
      let billNum = "";
      let actionName = "";
      switch (orderName) {
        case "CGRK":
          billNum = "instorage_id";
          name = "instorageSee";
          actionName = "inventoryoutinInstorageSeeACT";
          break;
        case "CGTH":
          billNum = "refund_id";
          name = "refundSee";
          actionName = "inventoryoutinRefundSeeACT";
          break;
        case "TBRK":
          billNum = "instorage_id";
          name = "threeinstOrageSee";
          actionName = "inventoryoutinThreeinstOrageSeeACT";
          break;
        case "QTRK":
          billNum = "instorage_id";
          name = "otheninstOrageSee";
          actionName = "inventoryoutinItheninstOrageSeeACT";
          break;
        case "DBRK":
          billNum = "instorage_id";
          name = "otheninstOrageSee";
          actionName = "inventoryoutinItheninstOrageSeeACT";
          break;
        case "PYRK":
          billNum = "instorage_id";
          name = "otheninstOrageSee";
          actionName = "inventoryoutinItheninstOrageSeeACT";
          break;
        case "TCYK":
          billNum = "transfer_id";
          name = "transferwareSee";
          actionName = "inventoryoutinTransferwareSeeACT";
          break;
        case "FTCK":
          billNum = "reborn_outstock_id";
          name = "auditreeoutSee";
          actionName = "inventoryoutinAuditreeoutSeeACT";
          break;
        case "XSDB":
          billNum = "transfer_id";
          name = "salesalLocationSee";
          actionName = "inventoryoutinSalesalLocationSeeACT";
          break;
        case "XSCK":
          billNum = "outstock_id";
          name = "salesOutSee";
          actionName = "inventoryoutinSalesOutSeeACT";
          break;
        case "XSTH":
          name = "salesReturnSee";
          billNum = "return_id";
          actionName = "inventoryoutinSalesReturnSeeACT";
          break;
        case "TBTH":
          name = "salesReturnSee";
          billNum = "return_id";
          actionName = "inventoryoutinSalesReturnSeeACT";
          break;
        case "TBCK":
          name = "threeoutstOrageSee";
          billNum = "outstock_id";
          actionName = "inventoryoutinThreeoutstOrageSeeACT";
          break;
        case "QTCK":
          name = "salesoutstorageSee";
          billNum = "outstock_id";
          actionName = "inventoryoutinSalesoutstorageSeeACT";
          break;
        case "DBCK":
          name = "salesoutstorageSee";
          billNum = "outstock_id";
          actionName = "inventoryoutinSalesoutstorageSeeACT";
          break;
        case "PYCK":
          name = "salesoutstorageSee";
          billNum = "outstock_id";
          actionName = "inventoryoutinSalesoutstorageSeeACT";
          break;
        case "PKCK": // 
          name = "salesoutstorageSee";
          billNum = "outstock_id";
          actionName = "inventoryoutinSalesoutstorageSeeACT";
          break;
        case "FTRK":
          this.$axMethod
            .getGoodsTire({
              goods_id: goods_id,
              order_sn: order_sn
            })
            .then(res => {
              if (res.data.code == 200) {
                this.GoodsTire = res.data.data;
                this.dialogFormVisibleSee = true;
              }
            });
          this.flag = true;
          break;
        case "WTDS":
          name = "salescommissionSee";
          billNum = "order_id";
          actionName = "inventoryoutinSalescommissionSeeACT";
          break;
        case "WTCK":
          name = "salessettlementSee";
          billNum = "outstock_id";
          actionName = "inventoryoutinSalessettlementSeeACT";
          break;
        case "JTTH":
          name = "tirebackstorageSee";
          billNum = "outstock_id";
          actionName = "inventoryoutinTirebackstorageSeeACT";
          break;
        case "PKCK":
          name = "salesoutstorageSee";
          billNum = "outstock_id";
          actionName = "inventoryoutinSalesoutstorageSeeACT";
          break;
        default:
          break;
      }
      if (this.flag) {
        return false;
      }
      this[actionName]({
        moduleName: name,
        [billNum]: Number(orderId),
        outstock_sn: row.order_sn
      });
    },
    getSummaries(param) {
      let _this = this;
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (
            [
              "income_num",
              "sell_num",
              "in_complete_num",
              "out_complete_num"
            ].includes(column.property)
          ) {
            sums[index] = sums[index];
          } else if (["income_price", "sell_price"].includes(column.property)) {
            sums[index] = sums[index].toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    },
    onSearch() {
      this.$refs.filterFormRef.validate(valid => {
        if (valid) {
          if (this.timeValue) {
            this.params.start_time = this.timeValue[0];
            this.params.end_time = this.timeValue[1];
          } else {
            this.params.start_time = this.params.end_time = "";
          }
          this.params.type = this.typelist;
          this.listShow = true;
          this.$set(this, "searchParams", {
            params: this.params, //1
            TableData: this.TableData, //
            API: "getInventoryRunningList", //
            vm: this //this
          });
          getData(this.searchParams, 2, tableResize.bind(this));
        }
      });
    },
    getWarehouse() {
      this.$common.options("getWarehouseListOption", this.warehouseList, {
        company_id: this.params.company_id
      }); //
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams, 2, tableResize.bind(this));
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams, 2, tableResize.bind(this));
    },
    companyFilter(val) {
      let options = {
        type: 1,
        company_name: val
      };
      getInventoryFinanceCompanyOpen(this, options);
    },
    getCompany() {
      let options = {
        type: 1,
        company_name: null
      };
      getInventoryFinanceCompanyOpen(this, options);
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.companyList = [
      {
        company_id: Number(localStorage.company_id),
        company_name: localStorage.company_name
      }
    ];

    //
    tableResize.bind(this)();
    window.onresize = tableResize.bind(this);
  }
};
</script>

