<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportRefundSee",
  data() {
    return {
      authority: {},
      TableData: {
        info: {}
      },
      checkPage: "",
      line: null
    };
  },
  methods: {
    ...mapActions([
      "inventoryoutinIndexACT",
      "refundAdd_show",
      "refundSearch_show",
      "refundauditDefault_show"
    ]),
    examineFun() {
      //
      this.$axios({
        method: "post",
        url: this.$api.refundAuditorPass,
        data: {
          refund_id: this.inventoryoutinRefundSeeGet.refund_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    abolishFun() {
      //
      this.$axios({
        method: "post",
        url: this.$api.refundAuditorInvalid,
        data: {
          refund_id: this.inventoryoutinRefundSeeGet.refund_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    goBack() {
      //   if (this.checkPage == "refundaudit") {
      //     this.refundauditDefault_show({
      //       moduleName: "refundauditDefault",
      //       params: this.inventoryoutinRefundSeeGet.params
      //     });
      //   } else {
      this.inventoryoutinIndexACT({
        moduleName: "Index",
        params: this.inventoryoutinRefundSeeGet.params
      });
      // }
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 6) {
            sums[index] = parseFloat(sums[index]);
          } else if (index === 7) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  computed: {
    ...mapGetters(["inventoryoutinRefundSeeGet"])
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    let vm = this;
    this.$axios({
      method: "get",
      url: vm.$api.getRefundInfo,
      params: {
        refund_id: this.inventoryoutinRefundSeeGet.refund_id
      }
    }).then(({ data }) => {
      if (data.code == 200) {
        this.$set(this, "TableData", data.data);
      }
    });
    this.checkPage = this.inventoryoutinRefundSeeGet.checkPage;
  }
};
</script>


