  <div class="seePage">
    <!--   -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack"></span>
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="TableData.info" class="demo-form-inline">
        <el-form-item label="">
          <p style="width:170px">{{TableData.info.company_name}}</p>
        </el-form-item>
        <el-form-item label="">
          <p style="width:170px">{{TableData.info.new_company_name}}</p>
        </el-form-item>
        <el-form-item label="">
          <p style="width:170px">{{TableData.info.business_time}}</p>
        </el-form-item>
        <el-form-item label="">
          <p style="width:170px">{{TableData.info.auditor_status_name}}</p>
        </el-form-item>
        <el-form-item label="">
          <p style="width:170px">{{TableData.info.transfer_sn}}</p>
        </el-form-item>
        <el-form-item label="">
          <p style="width:170px">{{TableData.info.add_user_name}}</p>
        </el-form-item>
        <el-form-item label="">
          <p style="width:170px">{{TableData.info.add_time}}</p>
        </el-form-item>
        <el-form-item label="">
          <p style="width:170px">{{TableData.info.remark}}</p>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_list" style="width: 100%" max-height="600">
        <el-table-column :show-overflow-tooltip="true" property="goods_id" min-width="160" label=""></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="/" min-width="130">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="" min-width="130">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="()" min-width="130">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="" min-width="130" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="new_warehouse_name" min-width="130" label="">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="new_allocation_name" min-width="130" label="">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="new_goods_num" min-width="110" label="" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="update_time" min-width="170" label="">
        </el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber"></div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="" nextText="" :total="TableData.total">
      </el-pagination>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">

      <!--  -->
      <template v-if="TableData.info.auditor_status == 1">
        <el-button type="primary" @click="audit('transferWarehouseAuidtorPass')" v-if="authority.b_pass==1"></el-button>
        <el-button @click="audit('transferWarehouseAuditorInvalid')" v-if="authority.b_invalid==1"></el-button>
      </template>
      <!--  -->
      <template v-if="TableData.info.auditor_status == 2 || TableData.info.auditor_status == 3 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack"></el-button>
      </template>
      <el-button @click="goBack" v-else></el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportTransferwareSee",
  data() {
    return {
      authority: {},
      TableData: {
        info: [],
        goods_list: []
      },
      params: {},
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      warehouseList: {
        options: []
      }, //
      allocationList: {
        options: []
      } //
    };
  },
  computed: {
    ...mapGetters(["transferwareIndexGet", "inventoryoutinTransferwareSeeGet"])
  },
  methods: {
    ...mapActions(["inventoryoutinIndexACT"]),
    goBack() {
      this.inventoryoutinIndexACT({
        moduleName: "Index"
      });
    },
    audit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          transfer_id: this.params.transfer_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    initData() {
      this.$axMethod.infoTransferBills(this.params).then(({ data }) => {
        if (data.code == "200") {
          console.log(data.data);
          this.TableData = data.data;
        }
      });
    },
    handleSizeChange(val) {
      this.params.per_page = val;
      this.initData();
    },
    handleCurrentChange(val) {
      this.params.page = val;
      this.initData();
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.params = {
      transfer_id: this.inventoryoutinTransferwareSeeGet.transfer_id
    };
    this.$common.options("getWarehouseListOption", this.warehouseList, {
      company_id: this.TableData.info.company_id
    }); //
    this.$common.options("getAllocationListOption", this.allocationList); //
    this.initData();
  }
};
</script>

