<template>
  <div class="seePage">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack"></span>
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.company_name}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.department_name}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.buyer_name}}</span>
        </el-form-item>
        <el-form-item label="()" size="mini">
          <span>{{TableData.instoragList.supplier_name}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.business_time}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.receivables_mode_name}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.warehouse_name}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.other_price_type_name}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.other_price}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.type}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <span>{{TableData.instoragList.auditor_status_name}}</span>
        </el-form-item>
        <el-form-item label="" size="mini">
          <el-input size="small" type="text" v-model="TableData.instoragList.remark" suffix-icon="el-icon-edit-outline" :maxlength="300" :disabled="true" class="boxTextarea" style="width: 200px;"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goodsList" :summary-method="getSummaries" show-summary>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" label="" width="160">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" min-width="250px" label="">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" min-width="500" label="">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="/" min-width="160">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" min-width="140px" label="" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="abrasion_price" min-width="140px" label="" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="intube_price" min-width="140px" label="" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="change_price" min-width="140px" label="" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="subtotal_price" min-width="140px" label="" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="" width="180px">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="other_price" label="" min-width="180" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="" min-width="160">
        </el-table-column>
      </el-table>
      <div class="refundauditFoot">
        <span>{{TableData.instoragList.total_price|priceFormat }}</span>
      </div>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <!--  -->
      <template v-if="TableData.instoragList.auditor_status == 1">
        <el-button type="primary" @click="audit('auditorPassThreeInStorage')" v-if="authority.b_pass==1"></el-button>
        <el-button @click="audit('auditorInvalidThreeInStorage')" v-if="authority.b_invalid==1"></el-button>
      </template>
      <!--  -->
      <template v-if="TableData.instoragList.auditor_status == 3 || TableData.instoragList.auditor_status == 2 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack"></el-button>
      </template>
      <el-button @click="goBack" v-else></el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportThreeinstOrageSee",
  data() {
    return {
      authority: {},
      TableData: {
        instoragList: {},
        goodsList: []
      }
    };
  },
  computed: { ...mapGetters(["inventoryoutinThreeinstOrageSeeGet"]) },
  methods: {
    ...mapActions(["inventoryoutinIndexACT"]),
    goBack() {
      this.inventoryoutinIndexACT({
        moduleName: "Index"
      });
    },
    audit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          instorage_id: this.instorage_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message.success(res.data.data);
          this.goBack();
        }
      });
    },
    // 
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = "";
          return;
        }

        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 5) {
            sums[index] = parseFloat(sums[index]);
          } else if ([6, 7, 8, 9].includes(index)) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
            if (index === 10) {
              this.$set(this.params, "total_price", sums[index]);
            }
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  mounted() {
    /**  */
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.instorage_id = this.inventoryoutinThreeinstOrageSeeGet.instorage_id;
    this.$axios({
      method: "get",
      url: this.$api.infoThreeInStorage,
      params: {
        instorage_id: this.instorage_id
      }
    }).then(res => {
      if (res.data.code == 200) {
        this.TableData.instoragList = res.data.data.instoragList;
        this.TableData.goodsList = res.data.data.goodsList;
      }
    });
  }
};
</script>

